import React, {useState, useEffect} from 'react';
import {useIntl} from 'react-intl';
import AppsContainer from '../../../@crema/core/AppsContainer';
// import AppComponentCard from '@crema/core/AppComponentCard';
import {AppComponentHeader} from '@crema';
import {TextField} from '@mui/material';
import {Box} from '@mui/system';
import {MobileDateRangePicker} from '@mui/lab';
import CAppContaner from 'components/CappContaner';
import {IconButton, Tooltip} from '@mui/material';

import ReactToPdf from 'react-to-pdf';
import FileDownloadIcon from '@mui/icons-material/FileDownload';
import {useLazyQuery} from '@apollo/client';
import {get_Order_Report} from 'query/orderReoprt/getOrder';
import ReportInvoiceOrder from 'pages/ecommerce/ReportInvoiceOrder';

const OrderReports = () => {
  const ref = React.createRef();

  const {messages} = useIntl();
  const [isLoading, setIsLoding] = useState(true);
  const [value, setValue] = useState([new Date(), new Date()]);
  const [getOrderReport, {loading, error, data}] =
    useLazyQuery(get_Order_Report);

  useEffect(() => {
    if (data) setIsLoding(false);
  }, [data]);
  useEffect(() => {
    console.log(error);
    console.log(data);
  }, [loading]);
  const handleSubmit = async () => {
    setIsLoding(true);
    let firstDate = '';
    let seconDate = '';
    if (value[0] != null) firstDate = dateForm(value[0]);
    if (value[1] != null) seconDate = dateForm(value[1]);
    getOrderReport({
      variables: {startDate: firstDate, endDate: seconDate, status: 'asdasd'},
    });
  };
  const options = {
    orientation: 'landscape',
    // unit: 'in',

    // format: [20,20]
  };
  const dateForm = (date) => {
    var dd = String(date.getDate()).padStart(2, '0');
    var mm = String(date.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = date.getFullYear();
    date = mm + '/' + dd + '/' + yyyy;
    return date;
  };
  return (
    <div ref={ref}>
      <>
        <AppComponentHeader
          cardStyle={{height: 'auto'}}
          fullView
          title={messages['sidebar.reports.order']}
        />
        <CAppContaner
          title={messages['Datepicker.title']}
          buttonCompMethod={handleSubmit}
          component={() => (
            <>
              <MobileDateRangePicker
                startText={messages['sidebar.reports.startdate']}
                endText={messages['sidebar.reports.enddate']}
                value={value}
                onChange={(newValue) => {
                  setValue(newValue);
                }}
                renderInput={(startProps, endProps) => (
                  <React.Fragment>
                    <TextField {...startProps} />
                    <Box sx={{mx: 2}}> to </Box>
                    <TextField {...endProps} />
                  </React.Fragment>
                )}
                buttonComp={(e) => console.log(e)}
              />
            </>
          )}
        />

        {!isLoading ? (
          <ReportInvoiceOrder data={data.getOrderReport} />
        ) : (
          <AppsContainer fullView></AppsContainer>
        )}
      </>
      <Box textAlign={'end'} id='downloadPdf'>
        <ReactToPdf
          targetRef={ref}
          filename='div-blue.pdf'
          options={options}
          x={0}
          y={0}
          scale={0.8}
        >
          {({toPdf}) => (
            <Tooltip title='Download PDF'>
              <IconButton onClick={toPdf}>
                <FileDownloadIcon color='primary' />
              </IconButton>
            </Tooltip>
          )}
        </ReactToPdf>
      </Box>
    </div>
  );
};

export default OrderReports;
